:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--test {
    background-color: transparent;
    color: #fff;
    padding: 2px 5px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;

    
}



.btn--smallp {
    padding: 8px 16px;
    font-size: 13px;
    font-family: 'Montserrat', sans-serif;
}

.btn--small {
    padding: 7px 18px;
    font-size: 12px;
    font-family: 'Montserrat', sans-serif;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;

}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;

}

.btn--large:hover,
.btn--medium:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
}
